@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;300;400;500;600;700&display=swap);
:root {
  --accent: #A8DEFF;
  --background: #07080F;
  --title-font: 'Josefin Sans', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: 'Josefin Sans', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #07080F;
  background: var(--background);
  color: white;
}
.home {
  min-height: 100vh;
}

.home nav {
  height: auto;
  display: flex;
  align-items: center;
  padding: 40px;
}

.home nav .name {
  color: var(--accent);
  font-size: 2rem;
  font-family: var(--title-font);
  line-height: 2.5rem;
  position: relative;
}

.home .works {
  padding: 0 40px;
  color: var(--accent);
}

.home .jobless {
  color: rgba(255, 255, 255, 0.274);
  padding: 0 5px;
}

.home .work-videos {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(320px, auto))[auto-fit];
      grid-template-columns: repeat(auto-fit, minmax(320px, auto));
  grid-gap: 30px;
  padding: 20px 0;
}

.home .work {
  border-radius: 6px;
  box-shadow: 0 0 25px 2px rgba(0, 0, 0, 0.849);
  overflow: hidden;
}

.home .work .cover-img {
  width: auto;
  height: 200px;
}

.home .work .cover-img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.home .work .about {
  padding: 15px 20px;
}

.home .work .title {
  font-size: 1.6rem;
  display: block;
  color: white;
  cursor: pointer;
}

.home .work .description {
  color: rgba(255, 255, 255, 0.623);
  display: block;
}

.home .work .dev-status {
  display: flex;
  align-items: center;
}

.home .work .dev-status .indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.home .work .dev-status span {
  margin-left: 8px;
}

.home .work .more-info {
  display: flex;
}

.home .work .link-logo {
  margin: 5px;
  width: 30px;
  height: 30px;
  object-fit: contain;
  cursor: pointer;
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 18px;
  color: var(--accent);
}
